<template>
  <div>
    <br>
     <v-card>
       <v-toolbar>
         <v-toolbar-title>
           <h1>Alerts ({{this.$store.getters.unviewed_alerts.length}})</h1>
         </v-toolbar-title>
         <v-spacer></v-spacer>
        <v-btn flat @click='closeAlert(null)' color='primary'dark>CLEAR ALL</v-btn>
       </v-toolbar>
      <v-container
        fluid
        grid-list-lg
      >
        <v-layout row wrap>
          <v-flex xs12 v-for='alert in this.$store.getters.unviewed_alerts' :id='"alert_"+alert["id"]'>
            <v-card color="blue-grey darken-2" class="white--text">
              <v-card-actions>
                <div>
                  <div class='font-weight-bold'>{{alert['msg']}}</div>
                </div>
                <v-spacer></v-spacer>
                <v-btn dark icon raised @click='closeAlert(alert["id"])'><v-icon>close</v-icon></v-btn>
                <v-btn dark icon raised :to='alert["go_to"]' @click='closeAlert(alert["id"])' :disabled='!alert["go_to"]'><v-icon>call_missed_outgoing</v-icon></v-btn>

              </v-card-actions>
            </v-card>
          </v-flex>

        </v-layout>
      </v-container>
    </v-card>






  </div>
</template>

<script>
export default {
  methods: {
    closeAlert (alert_id) {
      var ids = []
      if (alert_id) {
        ids.push(alert_id)

      }
      else {
        ids = this.$store.getters.alert_ids
      }

      for (var id in ids) {
        if (id > 0 && id.toString()) {
          var msg = {
            route: "bd.@md.alert.viewed",
            data: {
              'alert_id': id
            }
          }
          var s = this.$socket.emit('forward', msg)
          if (!s.connected) this.$store.dispatch('SET_ALERT', {'msg': 'Action not completed','id': 0, viewed: false})
        else
          this.$store.dispatch('SET_ALERT', {'id': id, viewed: true})
        }
      }
    }
  },
  data: () => {
    return {
      snackbar: false
    }
  }
}
</script>

